import React from "react";
import styles from "./GridViewPanel.module.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faWindowClose } from "@fortawesome/free-solid-svg-icons";

const gridViewPanel = (props) => {
  return (
    <div className={styles.row}>
      <div className={styles.panel} style={{ width: props.width }}>
        <div className={[styles.folderTop, styles.noSelect].join(" ")}>
          <div className={styles.heading}>
            <FontAwesomeIcon
              icon={faWindowClose}
              className={styles.icon}
              onClick={props.clickGridViewClose}
            />
            {props.heading}
          </div>
        </div>
        <div className={styles.folderBody}>{props.children}</div>
      </div>
    </div>
  );
};

export default gridViewPanel;
