import React, { Component, createRef } from "react";
import styles from "./MainViewer.module.css";
import { tileLayer, map, geoJSON, Browser } from "leaflet";
import "leaflet/dist/leaflet.css";
import MapViewerConfig from "../configurations/mapViewerConfig";
import SideBar from "./sideBar/SideBar";
import { DataContext } from "../store/context/data/DataContext";
import DropDown from "./dropDown/DropDown";
import GridViewPanel from "../gridViewPanel/GridViewPanel";
import Grid from "../grid/Grid";
import Backdrop from "@material-ui/core/Backdrop";
import CircularProgress from "@material-ui/core/CircularProgress";

class MainViewer extends Component {
  mainMapRef = createRef();

  initializeViewer = () => {
    this.props.setMapRef(this);
    const baseLayer = tileLayer(MapViewerConfig.baseLayers[1].url);
    this.map = map(this.mainMapRef.current, {
      center: MapViewerConfig.center,
      zoom: MapViewerConfig.zoom,
      layers: [baseLayer],
    });

    this.map.createPane("wmspane");
    this.map.getPane("wmspane").style.zIndex = 650;

    this.props.initializeViewer(this.map, baseLayer);
    //this.addWmsHandler();
    //console.log(this.props.data);
    //this.props.setGridViewPanel(true);
  };

  sideBarToggleHandler = () => {
    this.props.sideBarToggleHandler();
  };

  baseMapBtnHandler = (i) => {
    this.props.map.removeLayer(this.props.mapParams.selectedBaseLayer);

    const baseLayer =
      MapViewerConfig.baseLayers[i].type === "Google"
        ? tileLayer(MapViewerConfig.baseLayers[i].url, {
            subdomains: MapViewerConfig.baseLayers[i].subdomains,
          })
        : tileLayer(MapViewerConfig.baseLayers[i].url);
    this.props.changeBaseMapLayer(baseLayer);
  };
  highlightFeatureHandler = (e) => {
    var layer = e.target;

    layer.setStyle({
      weight: 5,
      fillColor: "#FFD700",
      color: "#FFD700",
      dashArray: "",
      fillOpacity: 1,
    });

    if (!Browser.ie && !Browser.opera && !Browser.edge) {
      layer.bringToFront();
    }
  };
  resetHighlightHandler = (e) => {
    this.props.mapParams.mapPolygon.resetStyle(e.target);
    //info.update();
  };
  featureClickHandler = (e) => {
    this.props.map.fitBounds(e.target.getBounds());
    const BdryNa = e.target.feature.properties.Districts;
    this.props.clickLabelPopup(BdryNa);
    //this.props.setGridViewPanel(true);
  };

  changeJsonStyle = (data, Json) => {
    //this.props.setGridViewPanel(true);
    if (this.props.mapParams.mapPolygon) {
      this.props.map.removeLayer(this.props.mapParams.mapPolygon);
    }
    this.addJsonHandler(Json, false);
    //console.log(data);
  };
  onEachFeatureHandler = (feature, layer) => {
    layer.on({
      mouseover: this.highlightFeatureHandler,
      mouseout: this.resetHighlightHandler,
      click: this.featureClickHandler,
    });
  };

  styleHandler = (feature) => {
    const provinceLower = feature.properties.Province.toLowerCase();
    const districtsLower = feature.properties.Dist_Name.toLowerCase();
    const titleCase = (str) => {
      str = str.toLowerCase().split(" ");
      for (var i = 0; i < str.length; i++) {
        str[i] = str[i].charAt(0).toUpperCase() + str[i].slice(1);
      }
      return str.join(" ");
    };
    const province = titleCase(provinceLower);
    const districts = titleCase(districtsLower);
    const districtsApi = this.props.data.changeData.districts;
    const provinceApi = this.props.data.changeData.province;
    const defaultColor = {
      fillColor: "white",
      weight: 2,
      opacity: 1,
      color: "#999",
      fillOpacity: 1,
    };
    const foundColor = {
      fillColor: "#116aab",
      weight: 2,
      opacity: 1,
      color: "#999",
      fillOpacity: 1,
    };
    //console.log(province);
    if (districtsApi[0] != null) {
      const check = districtsApi.includes(districts);
      if (check) {
        return foundColor;
      } else {
        return defaultColor;
      }
    } else {
      // console.log(provinceApi[0]);
      if (provinceApi[0] === "CountryWide") {
        return foundColor;
      } else {
        const checkProv = provinceApi.includes(province);
        if (checkProv) {
          return foundColor;
        } else {
          return defaultColor;
        }
      }
    }
  };
  addJsonHandler = (json, initial) => {
    if (this.props.mapParams.mapPolygon) {
      this.props.map.removeLayer(this.props.mapParams.mapPolygon);
    }
    const geoJsonLayer = geoJSON(json, {
      onEachFeature: this.onEachFeatureHandler,
      style: initial
        ? {
            fillColor: "white",
            weight: 2,
            opacity: 1,
            color: "#999",
            // dashArray: "3",
            fillOpacity: 1,
          }
        : this.styleHandler,
    });
    //console.log(geoJsonLayer);
    this.props.addJsonLayer(geoJsonLayer);
  };
  addWmsHandler = () => {
    if (this.props.mapParams.mapWmsLayer) {
      this.props.map.removeLayer(this.props.mapParams.mapWmsLayer);
    }
    const tileLayerConst = tileLayer.wms(
      "https://data.apps.fao.org/map/gsrv/edit/pak_handinhand/ows?",
      {
        layers: "pak_handinhand/:Op_Symb",
        transparent: true,
        format: "image/png",
        pane: "wmspane",
        tileSize: 1000,
      }
    );
    this.props.addLayerWms(tileLayerConst);
  };
  setGridViewPanel = () => {
    this.props.setGridViewPanel(false);
  };
  componentDidMount() {
    this.initializeViewer();
  }

  render = () => {
    return (
      <>
        {this.props.ui.loader ? (
          <Backdrop open={true} style={{ zIndex: 20 }}>
            <CircularProgress style={{ color: "#116aab" }} />
          </Backdrop>
        ) : null}
        <DropDown
          text={"Basemaps"}
          baseLayers={MapViewerConfig.baseLayers}
          clicked={this.baseMapBtnHandler}
          right={this.props.ui.sideBar ? "310px" : "10px"}
        />
        <SideBar
          sideBar={this.props.ui.sideBar}
          sideBarToggleHandler={() => {
            this.sideBarToggleHandler();
          }}
        />
        {this.props.ui.gridViewPanel ? (
          <GridViewPanel
            heading={"Grid View"}
            width={this.props.ui.sideBar ? "77.3%" : "99.2%"}
            // widthBody={this.props.ui.sideBar ? "90.5%" : "100%"}
            clickGridViewClose={this.setGridViewPanel}
          >
            <Grid gridData={this.props.data.grid} />
          </GridViewPanel>
        ) : null}

        <div ref={this.mainMapRef} className={styles.main}></div>
      </>
    );
  };
}

const mainViewer = (props) => (
  <DataContext.Consumer>
    {(mainConsumer) => <MainViewer {...mainConsumer} {...props} />}
  </DataContext.Consumer>
);

export default mainViewer;
