import React from "react";
import styles from "./Filters.module.css";
import DropDown from "../dropDownMaterialUi/DropDownMaterialUi";
import SubDropDown from "./subDropDown/SubDropDown";
import { DataContext } from "../../../../store/context/data/DataContext";

const Filters = (props) => {
  const drpDwnProjName = (v) => {
    if (v) {
      props.fetchChangeData(v.name);
      props.mainDrpDwnSetState("projName", false);
    }
  };

  const drpDwnProjTitle = (v) => {
    if (v) {
      props.fetchProjNameFromProjTitle(v);
      props.mainDrpDwnSetState("projTitle", false);
    }
  };

  const drpDwnDonor = async (v) => {
    await props.defaultView();
    if (v) {
      props.drpDwnDonorProj(false);
      props.fetchProjNameFromDonor(v);
      //Ui Close
      props.mainDrpDwnSetState("projDonor", false);

      //closeAllSubDrpDwns();
    }
  };

  const drpDwnDonorProj = (v) => {
    if (v) {
      props.fetchChangeData(v.name);
    }
  };

  const drpDwnDevArea = (v) => {
    if (v) {
      props.drpDwnDevAreaProj(false);
      props.fetchProjNameFromDevArea(v);
      props.mainDrpDwnSetState("projDevArea", false);
    }
  };

  const drpDwnDevAreaProj = (v) => {
    if (v) {
      props.fetchChangeData(v.name);
    }
  };

  const drpDwnThemArea = (v) => {
    if (v) {
      // props.mainDrpDwnSetState("projThemArea", false);
      drpDwnThemAreaSubTypeProjClose();
      props.fetchThemAreaSubFromThemArea(v);
    }
  };
  const drpDwnThemAreaSub = (v) => {
    if (v) {
      props.drpDwnSubTypeProjClose();
      props.fetchThemAreaSubTypeFromThemAreaSub(v);
    }
  };
  const drpDwnThemAreaSubType = (v) => {
    if (v) {
      props.drpDwnThemAreaSubTypeProj(false);
      props.fetchThemAreaSubTypeProjFromThemAreaSubType(v);
    }
  };

  const drpDwnThemAreaSubTypeProj = (v) => {
    if (v) {
      props.fetchChangeData(v.name);
    }
  };

  const drpDwnProv = (v) => {
    if (v) {
      drpDwnProvDistProjClose();
      if (v.name !== "Islamabad" && v.name !== "CountryWide") {
        props.mainDrpDwnSetState("projProv", false);
        props.fetchDistrict(v);
        props.fetchChangeDataForProv(v.name);
        // props.mainDrpDwnSetState("projProv", false);
      }
    }
  };

  const drpDwnProvDist = (v) => {
    if (v) {
      props.drpDwnProvDistProj(false);
      props.fetchProjectsForDrpDwn(v);
      props.applyChangeDataForDist(v.name);
    }
  };
  const drpDwnProvDistProj = (v) => {
    if (v) {
      props.fetchChangeData(v.name);
    }
  };
  const drpDwnDonorProjClose = async () => {
    await props.drpDwnDonorProj(false);
    await props.defaultView();
    await props.setGridViewPanel(false);
  };
  const drpDwnDevAreaProjClose = async () => {
    await props.drpDwnDevAreaProj(false);
    await props.defaultView();
    await props.setGridViewPanel(false);
  };
  const drpDwnThemAreaSubTypeProjClose = async () => {
    await props.defaultView();
    await props.drpDwnThemAreaSubTypeProjClose();
    await props.setGridViewPanel(false);
    // props.defaultView();
  };
  const drpDwnProvDistProjClose = async () => {
    await props.drpDwnProvDistProjClose();
    await props.defaultView();
    await props.setGridViewPanel(false);
  };
  // const closeAllSubDrpDwns = () => {
  //   props.drpDwnDonorProj(false);
  //   props.drpDwnDevAreaProj(false);
  //   props.drpDwnThemAreaSubTypeProjClose();
  //   props.drpDwnProvDistProjClose();
  // };
  return (
    <>
      <div className={styles.row}>
        <div className={styles.wrapDiv}>
          {props.mdrpDwnProjName ? (
            <div className={styles.dropDwnDiv}>
              <div className={styles.headingDropDwn}>
                Select By Project Symbol:
              </div>
              <div className={styles.dropDwn}>
                <DropDown
                  dropDwnList={props.dropDwnList.projName}
                  drpDwnChange={drpDwnProjName}
                  placeHolder={"Project Symbol"}
                  fontSize={"0.7rem"}
                  width={200}
                />
              </div>
            </div>
          ) : null}
          {props.mdrpDwnProjTitle ? (
            <div className={styles.dropDwnDiv}>
              <div className={styles.headingDropDwn}>
                Select By Project Name:
              </div>
              <div className={styles.dropDwn}>
                <DropDown
                  dropDwnList={props.dropDwnList.projTitle}
                  drpDwnChange={drpDwnProjTitle}
                  placeHolder={"Project Name"}
                  fontSize={"0.7rem"}
                  width={200}
                />
              </div>
            </div>
          ) : null}
          {props.mdrpDwnProjDonor ? (
            <SubDropDown
              labelDrpDwn={"Select By Donor"}
              labelDrpDwn1={"Select Project"}
              placeHolderDropDown={"Donor"}
              placeHolderDropDown1={"Project"}
              dropDwnList={props.dropDwnList.projDonor}
              dropDwnList1={props.dropDwnList.projDonorProj}
              drpDwnChange={drpDwnDonor}
              drpDwnChange1={drpDwnDonorProj}
              drpDwn1={props.drpDwn.drpDwnDonorProj}
              drpDwn2={false}
              drpDwn3={false}
              drpDwnClose={drpDwnDonorProjClose}
            />
          ) : null}
          {props.mdrpDwnProjDevArea ? (
            <SubDropDown
              labelDrpDwn={"Select By Development Area"}
              labelDrpDwn1={"Select Project"}
              placeHolderDropDown={"Development Area"}
              placeHolderDropDown1={"Project"}
              dropDwnList={props.dropDwnList.devAreaTypeCategory}
              dropDwnList1={props.dropDwnList.devAreaTypeCategoryProj}
              drpDwnChange={drpDwnDevArea}
              drpDwnChange1={drpDwnDevAreaProj}
              drpDwn1={props.drpDwn.drpDwnDevAreaProj}
              drpDwn2={false}
              drpDwn3={false}
              drpDwnClose={drpDwnDevAreaProjClose}
            />
          ) : null}
          {props.mdrpDwnProjThemArea ? (
            <SubDropDown
              labelDrpDwn={"Select By Thematic Area"}
              labelDrpDwn1={"Select Sub Thematic Area"}
              labelDrpDwn2={"Select Sub Thematic Area Type"}
              labelDrpDwn3={"Select Project"}
              placeHolderDropDown={"Thematic Area"}
              placeHolderDropDown1={"Sub Thematic Area"}
              placeHolderDropDown2={"Sub Thematic Area Type"}
              placeHolderDropDown3={"Project"}
              dropDwnList={props.dropDwnList.themArea}
              dropDwnList1={props.dropDwnList.themAreaSub}
              dropDwnList2={props.dropDwnList.themAreaSubType}
              dropDwnList3={props.dropDwnList.themAreaSubTypeProj}
              drpDwnChange={drpDwnThemArea}
              drpDwnChange1={drpDwnThemAreaSub}
              drpDwnChange2={drpDwnThemAreaSubType}
              drpDwnChange3={drpDwnThemAreaSubTypeProj}
              drpDwn1={props.drpDwn.drpDwnThemAreaSub}
              drpDwn2={props.drpDwn.drpDwnThemAreaSubType}
              drpDwn3={props.drpDwn.drpDwnThemAreaSubTypeProj}
              drpDwnClose={drpDwnThemAreaSubTypeProjClose}
            />
          ) : null}
          {props.mdrpDwnProjProv ? (
            <SubDropDown
              labelDrpDwn={"Select By Province"}
              labelDrpDwn1={"Select District"}
              labelDrpDwn2={"Select Project"}
              placeHolderDropDown={"Province"}
              placeHolderDropDown1={"District"}
              placeHolderDropDown2={"Project"}
              dropDwnList={props.dropDwnList.projProvince}
              dropDwnList1={props.dropDwnList.projDistricts}
              dropDwnList2={props.dropDwnList.projDistrictProj}
              drpDwnChange={drpDwnProv}
              drpDwnChange1={drpDwnProvDist}
              drpDwnChange2={drpDwnProvDistProj}
              drpDwn1={props.drpDwn.drpDwnProvDist}
              drpDwn2={props.drpDwn.drpDwnProvDistProj}
              drpDwn3={false}
              drpDwnClose={drpDwnProvDistProjClose}
            />
          ) : null}
        </div>
      </div>
    </>
  );
};

const filters = (props) => (
  <DataContext.Consumer>
    {(mainConsumer) => (
      <Filters
        {...props}
        fetchDistrict={mainConsumer.fetchDistrict}
        fetchProjectsForDrpDwn={mainConsumer.fetchProjectsForDrpDwn}
        fetchChangeDataForProv={mainConsumer.fetchChangeDataForProv}
        applyChangeDataForDist={mainConsumer.applyChangeDataForDist}
        drpDwn={mainConsumer.ui.drpDwn}
        drpDwnProvDist={mainConsumer.drpDwnProvDist}
        drpDwnProvDistProj={mainConsumer.drpDwnProvDistProj}
        fetchChangeData={mainConsumer.fetchChangeData}
        drpDwnProvDistProjClose={mainConsumer.drpDwnProvDistProjClose}
        defaultView={mainConsumer.defaultView}
        fetchProjNameFromProjTitle={mainConsumer.fetchProjNameFromProjTitle}
        fetchProjNameFromDonor={mainConsumer.fetchProjNameFromDonor}
        drpDwnDonorProj={mainConsumer.drpDwnDonorProj}
        drpDwnDevAreaProj={mainConsumer.drpDwnDevAreaProj}
        fetchProjNameFromDevArea={mainConsumer.fetchProjNameFromDevArea}
        drpDwnThemAreaSubTypeProjClose={
          mainConsumer.drpDwnThemAreaSubTypeProjClose
        }
        fetchThemAreaSubFromThemArea={mainConsumer.fetchThemAreaSubFromThemArea}
        fetchThemAreaSubTypeFromThemAreaSub={
          mainConsumer.fetchThemAreaSubTypeFromThemAreaSub
        }
        drpDwnThemAreaSubType={mainConsumer.drpDwnThemAreaSubType}
        fetchThemAreaSubTypeProjFromThemAreaSubType={
          mainConsumer.fetchThemAreaSubTypeProjFromThemAreaSubType
        }
        drpDwnThemAreaSubTypeProj={mainConsumer.drpDwnThemAreaSubTypeProj}
        drpDwnSubTypeProjClose={mainConsumer.drpDwnSubTypeProjClose}
        mdrpDwnProjName={mainConsumer.ui.drpDwn.mdrpDwnProjName}
        mdrpDwnProjTitle={mainConsumer.ui.drpDwn.mdrpDwnProjTitle}
        mdrpDwnProjDonor={mainConsumer.ui.drpDwn.mdrpDwnProjDonor}
        mdrpDwnProjDevArea={mainConsumer.ui.drpDwn.mdrpDwnProjDevArea}
        mdrpDwnProjThemArea={mainConsumer.ui.drpDwn.mdrpDwnProjThemArea}
        mdrpDwnProjProv={mainConsumer.ui.drpDwn.mdrpDwnProjProv}
        mainDrpDwnSetState={mainConsumer.mainDrpDwnSetState}
        setGridViewPanel={mainConsumer.setGridViewPanel}
      />
    )}
  </DataContext.Consumer>
);

export default filters;
