import React from "react";
import styles from "./SideBar.module.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAngleDoubleLeft,
  faAngleDoubleRight,
} from "@fortawesome/free-solid-svg-icons";
import Accordion from "./accordion/Accordion";
import { DataContext } from "../../store/context/data/DataContext";

const SideBar = (props) => {
  return (
    <>
      {props.sideBar ? null : (
        <div
          className={styles.sideBarBtnAbsolute}
          onClick={props.sideBarToggleHandler}
        >
          <div className={styles.iconStyle}>
            <FontAwesomeIcon icon={faAngleDoubleLeft} />
          </div>
        </div>
      )}
      <div
        className={[
          styles.sideBar,
          props.sideBar ? styles.sideBarOpen : styles.sideBarClose,
        ].join(" ")}
        style={{ height: props.height }}
      >
        <div className={styles.row}>
          <div className={[styles.col, styles.btnSpan1of2].join(" ")}>
            <div
              className={styles.sideBarBtn}
              onClick={props.sideBarToggleHandler}
            >
              <div className={styles.iconStyle}>
                <FontAwesomeIcon icon={faAngleDoubleRight} />
              </div>
            </div>
          </div>
          <div className={[styles.col, styles.btnSpan2of2].join(" ")}>
            <div className={styles.panelDiv}>
              {props.dropDwnList ? (
                <Accordion
                  dropDwnList={props.dropDwnList}
                  stats={props.stats}
                />
              ) : null}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

const sideBar = (props) => (
  <DataContext.Consumer>
    {(mainConsumer) => (
      <SideBar
        {...props}
        dropDwnList={mainConsumer.data.dropDwn}
        stats={mainConsumer.stats}
      />
    )}
  </DataContext.Consumer>
);

export default sideBar;
