import React from "react";
import styles from "./Grid.module.css";

const Grid = (props) => {
  const typeCatHead = [
    { class: "Agriculture Production", subClass: "Crops", type: "Wheat" },
    { class: "Agriculture Production", subClass: "Crops", type: "Vegetable" },
    { class: "Agriculture Production", subClass: "Crops", type: "Maize" },
    { class: "Agriculture Production", subClass: "Crops", type: "Pulses" },
    {
      class: "Agriculture Production",
      subClass: "Crops",
      type: "High value crops",
    },
    { class: "Agriculture Production", subClass: "Crops", type: "Cotton" },
    { class: "Agriculture Production", subClass: "Crops", type: "Sugarcane" },
    { class: "Agriculture Production", subClass: "Livestock", type: "Diary" },
    {
      class: "Agriculture Production",
      subClass: "Livestock",
      type: "Animal Health",
    },
    { class: "Agriculture Production", subClass: "Livestock", type: "Feed" },
    {
      class: "Agriculture Production",
      subClass: "Livestock",
      type: "Breeding",
    },
    {
      class: "Agriculture Production",
      subClass: "Livestock",
      type: "Production",
    },

    { class: "Agriculture Production", subClass: "Poultary", type: "Broiler" },
    { class: "Agriculture Production", subClass: "Poultary", type: "Layers" },
    {
      class: "Agriculture Production",
      subClass: "Poultary",
      type: "Hatcheries",
    },
    { class: "Agriculture Production", subClass: "Forestry", type: "SFM" },
    {
      class: "Agriculture Production",
      subClass: "Forestry",
      type: "Agro-Forestry",
    },
    {
      class: "Agriculture Production",
      subClass: "Forestry",
      type: "Protected areas",
    },
    {
      class: "Agriculture Production",
      subClass: "Fisheries/Aquaculture",
      type: "Aquaculture",
    },
    {
      class: "Agriculture Production",
      subClass: "Fisheries/Aquaculture",
      type: "Fish Ponds",
    },
    {
      class: "Agriculture Production",
      subClass: "Fisheries/Aquaculture",
      type: "Sus. capture fishries",
    },
    {
      class: "Agri-business Development and Livelihoods Support",
      subClass: "Agricultural services",
      type: "Extension",
    },
    {
      class: "Agri-business Development and Livelihoods Support",
      subClass: "Agricultural services",
      type: "Skills development",
    },
    {
      class: "Agri-business Development and Livelihoods Support",
      subClass: "Agricultural services",
      type: "Access to finance",
    },
    {
      class: "Agri-business Development and Livelihoods Support",
      subClass: "Agricultural services",
      type: "Research",
    },
    {
      class: "Agri-business Development and Livelihoods Support",
      subClass: "Agri-business",
      type: "Post harvest",
    },
    {
      class: "Agri-business Development and Livelihoods Support",
      subClass: "Agri-business",
      type: "Value chain development",
    },
    {
      class: "Agri-business Development and Livelihoods Support",
      subClass: "Agri-business",
      type: "Private sector development",
    },
    {
      class: "Agri-business Development and Livelihoods Support",
      subClass: "Agri-business",
      type: "MSMEs",
    },
    {
      class: "Agri-business Development and Livelihoods Support",
      subClass: "Agri-business",
      type: "Processing",
    },
    {
      class: "Agri-business Development and Livelihoods Support",
      subClass: "Non-farm income",
      type: "Income generation",
    },
    {
      class: "Agri-business Development and Livelihoods Support",
      subClass: "Non-farm income",
      type: "Rural employment",
    },
    {
      class: "Agri-business Development and Livelihoods Support",
      subClass: "Safetynet/Insurance",
      type: "Cash/Voucher",
    },
    {
      class: "Agri-business Development and Livelihoods Support",
      subClass: "Safetynet/Insurance",
      type: "Social protection",
    },
    {
      class: "Development Area",
      subClass: null,
      type: "Climate Change Adaptation",
    },
    {
      class: "Development Area",
      subClass: null,
      type: "Climate Change Mitigation",
    },
    { class: "Development Area", subClass: null, type: "ICT" },
    { class: "Development Area", subClass: null, type: "Land" },
    {
      class: "Development Area",
      subClass: null,
      type: "Food waste & Food loss",
    },
    { class: "Development Area", subClass: null, type: "Food safety" },
    { class: "Development Area", subClass: null, type: "Health" },
    { class: "Development Area", subClass: null, type: "Water" },
    { class: "Development Area", subClass: null, type: "Biodiversity" },
    {
      class: "Development Area",
      subClass: null,
      type: "Ecosystem restoration",
    },
    {
      class: "Development Area",
      subClass: null,
      type: "Food security & Nutrition",
    },
    { class: "Development Area", subClass: null, type: "Rural energy" },
    { class: "Development Area", subClass: null, type: "Rural infrastructure" },
    { class: "Development Area", subClass: null, type: "Policy" },
    {
      class: "Development Area",
      subClass: null,
      type: "Disaster Risk Management",
    },
    { class: "Development Area", subClass: null, type: "Social development" },
    { class: "Development Area", subClass: null, type: "Education" },
  ];
  const headings = [
    { name: "Project Symbol" },
    { name: "Project Name" },
    { name: "Province" },
    { name: "Districts" },
    { name: "Beneficiaries" },
    { name: "SDGS" },
    { name: "Donor" },
    { name: "Implementing Agency" },
    { name: "Duration" },
    { name: "Govt Policies" },
    { name: "Project Development Objective" },
    { name: "OutComes" },
    { name: "Main Interventions" },
  ];
  return (
    <div className={styles.tableDiv}>
      <table className={styles.table}>
        <tbody>
          <tr>
            {headings.map((k, i) => {
              return (
                <>
                  <th
                    className={styles.tableHeadings}
                    key={"headings" + k.name + i}
                  >
                    {k.name}
                  </th>
                </>
              );
            })}
            {typeCatHead.map((k, i) => {
              const classC = k.class;
              const subclass = k.subClass;
              const typec = k.type;
              return (
                <>
                  <th
                    className={styles.tableHeadings}
                    key={"headings" + k.type + i}
                  >
                    {subclass === null
                      ? classC + "/" + typec
                      : classC + "/" + subclass + "/" + typec}
                  </th>
                </>
              );
            })}
          </tr>

          <tr>
            <td className={styles.colStyle}>{props.gridData.projName}</td>
            <td className={styles.colStyle}>{props.gridData.projTitle}</td>
            <td className={styles.colStyle}>{props.gridData.prov}</td>
            <td className={styles.colStyle}>{props.gridData.districts}</td>
            <td className={styles.colStyle}>{props.gridData.beneficiaries}</td>
            <td className={styles.colStyle}>{props.gridData.sdgs}</td>
            <td className={styles.colStyle}>{props.gridData.donor}</td>
            <td className={styles.colStyle}>{props.gridData.impAgency}</td>
            <td className={styles.colStyle}>{props.gridData.duration}</td>
            <td className={styles.colStyle}>{props.gridData.govtPolicy}</td>
            <td className={styles.colStyle}>{props.gridData.projObj}</td>
            <td className={styles.colStyle}>{props.gridData.outComes}</td>
            <td className={styles.colStyle}>{props.gridData.interventions}</td>
            {props.gridData.typeCategory
              ? typeCatHead.map((k, i) => {
                  let check = false;
                  // props.gridData.typeCategory.forEach((sk, si) => {
                  //   //console.log(sk);

                  //   if (k.type === sk) {
                  //     check === true;
                  //   }
                  // });

                  props.gridData.typeCategory.forEach((sk) => {
                    if (k.type === sk) {
                      check = true;
                    }
                  });
                  return check ? (
                    <td className={styles.colStyle}>Yes</td>
                  ) : (
                    <td className={styles.colStyle}>No</td>
                  );
                  //console.log(check);
                })
              : null}
          </tr>
        </tbody>
      </table>
    </div>
  );
};

export default Grid;
