import React from "react";
import styles from "./Summary.module.css";
import CountUp from "react-countup";

const summary = (props) => {
  // console.log(props.dropDwnList.projName.length);
  const data = [
    { name: "Country Wide", value: props.stats.countryWide },
    { name: "ICT", value: props.stats.ict },
    { name: "Punjab", value: props.stats.punjab },
    { name: "Sindh", value: props.stats.sindh },
    { name: "KP", value: props.stats.kp },
    { name: "Gilgit Baltistan", value: props.stats.gb },
    { name: "Balochistan", value: props.stats.balochistan },
  ];
  return (
    <>
      <div className={styles.row}>
        <div className={styles.total}>
          <div className={styles.totalDiv}>
            <div className={styles.valueTotal}>
              {props.stats ? (
                //  props.stats.totalProj
                <CountUp end={props.stats.totalProj} duration={5.75} />
              ) : null}
            </div>
            <div className={styles.valueLabel}>Total Projects</div>
          </div>
        </div>
      </div>

      <div className={styles.row}>
        <div className={styles.summaryProvDiv}>
          {props.stats
            ? data.map((k, i) => {
                return (
                  <div className={styles.provDiv} key={"provDiv" + k.name}>
                    <div className={styles.row}>
                      <div className={[styles.col, styles.span1of2].join(" ")}>
                        <div className={styles.provLabel}>{k.name}</div>
                      </div>
                      <div className={[styles.col, styles.span2of2].join(" ")}>
                        <div className={styles.provValue}>
                          <CountUp end={k.value} duration={5.75} />
                        </div>
                      </div>
                    </div>
                  </div>
                );
              })
            : null}
        </div>
      </div>
    </>
  );
};

export default summary;
