import React from "react";
import styles from "./SubDropDown.module.css";
import DropDown from "../../dropDownMaterialUi/DropDownMaterialUi";
import { Animated } from "react-animated-css";

const subDropDown = (props) => {
  // console.log(props.drpDwn1);

  return (
    <>
      <div className={styles.dropDwnDiv}>
        <div className={styles.headingDropDwn}>
          {props.labelDrpDwn}
          {props.drpDwn1 ? (
            <span className={styles.close} onClick={props.drpDwnClose}>
              Close
            </span>
          ) : null}
        </div>
        <div className={styles.dropDwn}>
          <DropDown
            dropDwnList={props.dropDwnList}
            drpDwnChange={props.drpDwnChange}
            placeHolder={props.placeHolderDropDown}
            fontSize={"0.7rem"}
            width={200}
          />
        </div>
      </div>
      {props.drpDwn1 ? (
        <>
          <Animated
            animationIn="bounce"
            animationOut="slideOutUp"
            animationInDuration={1000}
            animationOutDuration={1000}
            isVisible={true}
          >
            <div className={styles.dropDwnDiv1}>
              <div className={styles.headingDropDwn1}>{props.labelDrpDwn1}</div>
              <div className={styles.dropDwn1}>
                <DropDown
                  dropDwnList={props.dropDwnList1}
                  drpDwnChange={props.drpDwnChange1}
                  placeHolder={props.placeHolderDropDown1}
                  fontSize={"0.7rem"}
                  width={200}
                />
              </div>
            </div>
          </Animated>
        </>
      ) : null}
      {props.drpDwn2 ? (
        <>
          <Animated
            animationIn="bounce"
            animationOut="slideOutUp"
            animationInDuration={1000}
            animationOutDuration={1000}
            isVisible={true}
          >
            <div className={styles.dropDwnDiv1}>
              <div className={styles.headingDropDwn1}>{props.labelDrpDwn2}</div>
              <div className={styles.dropDwn1}>
                <DropDown
                  dropDwnList={props.dropDwnList2}
                  drpDwnChange={props.drpDwnChange2}
                  placeHolder={props.placeHolderDropDown2}
                  fontSize={"0.7rem"}
                  width={200}
                />
              </div>
            </div>
          </Animated>
        </>
      ) : null}
      {props.drpDwn3 ? (
        <>
          <Animated
            animationIn="bounce"
            animationOut="slideOutUp"
            animationInDuration={1000}
            animationOutDuration={1000}
            isVisible={true}
          >
            <div className={styles.dropDwnDiv1}>
              <div className={styles.headingDropDwn1}>{props.labelDrpDwn3}</div>
              <div className={styles.dropDwn1}>
                <DropDown
                  dropDwnList={props.dropDwnList3}
                  drpDwnChange={props.drpDwnChange3}
                  placeHolder={props.placeHolderDropDown3}
                  fontSize={"0.7rem"}
                  width={200}
                />
              </div>
            </div>
          </Animated>
        </>
      ) : null}
    </>
  );
};

export default subDropDown;
